:root {
   --noise: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='9.97' numOctaves='6' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");

   --color-black: hsla(0, 0%, 8%, 1);
   --color-black-dim: hsla(0, 0%, 10%, 1);
   --color-black-trans10: hsla(0, 0%, 0%, 0.1);
   --color-black-trans25: hsla(0, 0%, 0%, 0.25);
   --color-gray-1: hsl(0, 0%, 30%);
   --color-gray-2: hsl(0, 0%, 60%);
   --color-gray-3: hsl(0, 0%, 90%);
   --color-gray-4: hsl(0, 0%, 95%);
   --color-white: hsla(0, 0%, 98%, 1);
   --color-white-dim: hsl(0, 0%, 85%);
   --color-white-trans25: hsla(0, 0%, 100%, 0.25);
   --color-yellow-1: hsl(52, 100%, 39%);
   --color-yellow-2: hsl(52, 100%, 59%);
   --color-yellow-3: hsl(53, 69%, 87%);

   --color-body-light: hsl(var(--primary-color-h), 60%, 92%);
   --color-body-dark: hsl(var(--primary-color-h), 40%, 15%);
   --color-button-inset-glow: hsla(0, 0%, 100%, 0.6);
   --color-button-inset-glow-hover: hsla(0, 0%, 100%, 0.9);
   --color-header-light: hsl(var(--primary-color-h), 100%, 95%);
   --color-header-dark: hsl(var(--primary-color-h), 30%, 20%);

   --primary-color-h: 221;
   --primary-color-s: 100%;
   --primary-color-l: 55%;
   --secondary-color-h: 271;
   --secondary-color-s: 76%;
   --secondary-color-l: 30%;

   --lightenChange: 35%;
   --lightenChange-2: 60%;
   --darkenChange: 25%;

   --color-primary: hsl(var(--primary-color-h), var(--primary-color-s), var(--primary-color-l));
   --color-primary-complement: hsl(calc(var(--primary-color-h) + 180), var(--primary-color-s), var(--primary-color-l));
   --color-primary-dark: hsl(var(--primary-color-h), var(--primary-color-s), var(--primary-color-dark-l));
   --color-primary-light: hsl(var(--primary-color-h), var(--primary-color-s), var(--primary-color-light-l));
   --color-primary-triad-1: hsl(calc(var(--primary-color-h) + 120), var(--primary-color-s), var(--primary-color-l));
   --color-primary-triad-2: hsl(calc(var(--primary-color-h) - 120), var(--primary-color-s), var(--primary-color-l));
   --primary-color-light-l: calc(var(--primary-color-l) + var(--lightenChange));
   --primary-color-dark-l: calc(var(--primary-color-l) - var(--darkenChange));

   --color-secondary: hsl(var(--secondary-color-h), var(--secondary-color-s), var(--secondary-color-l));
   --color-secondary-complement: hsl(calc(var(--secondary-color-h) + 180), var(--secondary-color-s), var(--secondary-color-l));
   --color-secondary-dark: hsl(var(--secondary-color-h), var(--secondary-color-s), var(--secondary-color-dark-l));
   --color-secondary-light: hsl(var(--secondary-color-h), var(--secondary-color-s), var(--secondary-color-light-l));
   --color-secondary-light-2: hsl(var(--secondary-color-h), var(--secondary-color-s), var(--secondary-color-light-2));
   --color-secondary-triad-1: hsl(calc(var(--secondary-color-h) + 120), var(--secondary-color-s), var(--secondary-color-l));
   --color-secondary-triad-2: hsl(calc(var(--secondary-color-h) - 120), var(--secondary-color-s), var(--secondary-color-l));
   --secondary-color-light-l: calc(var(--secondary-color-l) + var(--lightenChange));
   --secondary-color-light-2: calc(var(--secondary-color-l) + var(--lightenChange-2));
   --secondary-color-dark-l: calc(var(--secondary-color-l) - var(--darkenChange));
}

