   "57": 57rem,
   "58": 58rem,
   "59": 59rem,
   "60": 60rem,
   "61": 61.65rem
);

@mixin mq($width, $type: min) {
   @if map_has_key($breakpoints, $width) {
      $width: map_get($breakpoints, $width);

      @if $type =max {
         $width: $width - 0.1rem;
      }

      @media only screen and (#{$type}-width: $width) {
         @content;
      }
   }
}

@mixin transitions-standard {
   transition: background-color 0.25s, color 0.5s, letter-spacing 0s;
}

/* HTML ELEMENTS: GENERAL */

* {
   box-sizing: border-box;
}

button,
.button,
cite,
input,
keygen select,
textarea {
   @include font-family-sans-rounded;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
dl,
table {
   font-size: 1rem;
   margin: 1rem 0 0 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
   line-height: 1.5;
}

/* HTML ELEMENTS: SPECIFIC */

a[href^="http://"]:not(.button),
a[href^="https://"]:not(.button) {
   &:after {
      background-image: url(/img/external-link.svg);
      background-position: bottom left;
      background-repeat: no-repeat;
      background-size: 80% 80%;
      content: "";
      display: inline-block;
      height: 0.9rem;
      margin-left: 0.25rem;
      opacity: 0.5;
      vertical-align: baseline;
      width: 0.9rem;
   }

   &:hover {
      &:after {
         opacity: 0.9;
      }
   }
}

abbr {
   cursor: help;
}

body {
   @include font-family-serif;
   background-position: center -4rem !important;
   background-size: 16rem !important;
   font-size: 16px;
   font-weight: 400;
   hanging-punctuation: first;
   line-height: 1.65;
   margin: 0;
   padding: 1rem 0 1.5rem 0;
   padding: 1rem env(safe-area-inset-right) 0 env(safe-area-inset-left);
   position: relative;
}

button,
.button {
   @include border-radius;
   border: none;
   cursor: pointer;
   display: inline-block;
   margin: 1rem 0.1rem 0 0;
   padding: 0.5rem 0.75rem;
   text-decoration: none;
   vertical-align: middle;

   &.selected {
      text-decoration: underline;
   }
}

cite {
   display: block;
   font-size: 65%;
   font-weight: 400;
   text-transform: uppercase;

   &.dash {
      &::after {
         content: " â€” ";
         display: none;
      }
   }
}

details {
   cursor: pointer;

   summary:hover {
      text-decoration: underline;
   }
}

em,
i {
   font-style: oblique;
}

footer {
   container: footer / inline-size;
   display: block;
   font-size: 90%;
   margin: 1.25rem 0 0 0;
   overflow: hidden;
   padding: 0 1rem 2rem 1rem;

   @include mq("33") {
      margin: 0 auto;
      padding: 0 0 1rem 0;
   }

   h2,
   h3,
   h4,
   h5 {
      font-size: 105%;
      font-weight: 600;
      margin: 1.5rem 0 0 0;
      padding: 0;
   }

   &.flexContainer {
      align-content: flex-start;
      align-items: flex-start;
      display: flex;
      flex-flow: row wrap;
      gap: 4%;
      justify-content: space-between;
      margin: 0 auto;
      overflow: hidden;
      width: 100%;

      @include mq("33") {
         max-width: 78rem;
      }

      @include mq("44", $type: max) {
         padding: 0 1rem 1rem 1rem;
      }

      @include mq("44") {
         padding: 0 2rem 1rem 2rem;
      }

      .flexCard {
         width: 100%;

         &.cardOne {
            @include mq("48") {
               max-width: 40%;
            }
         }

         &.cardTwo,
         &.cardThree,
         &.cardFour {
            @include mq("33") {
               flex: 1;
            }

            a {
               display: inline-block;
            }
         }
      }

      .favoriteLinks {
         li {
            list-style: none;
            position: relative;

            &.Bluesky::before {
               left: -1.4rem;
               top: 0.3rem;
            }

            &.LinkedIn::before {
               left: -1.4rem;
               top: 0.2rem;
            }

            &.Mastodon::before {
               left: -1.4rem;
               top: 0.25rem;
            }

            &.Threads::before {
               left: -1.4rem;
               top: 0.25rem;
            }

            &.VSCO::before {
               left: -1.45rem;
               top: 0.2rem;
            }
            
            &.Ars::before {
               left: -1.4rem;
               top: 0.2rem;
            }
            
            &.Hac::before {
               left: -1.4rem;
               top: 0.3rem;
            }
            
            &.Mac::before {
               left: -1.4rem;
               top: 0.3rem;
            }
            
            &.Tec::before {
               left: -1.4rem;
               top: 0.2rem;
            }

            &::before {
               background-color: transparent;
               background-position: top left;
               background-repeat: no-repeat;
               background-size: contain;
               content: '';
               height: 1rem;
               position: absolute;
               width: 1rem;
            }
         }
      }

      .footerIcons {
         display: flex;
         gap: 0.5rem;
         list-style: none;
         margin: 1rem 0 0 0;
         padding: 0;

         li {
            margin: 0;
            padding: 0;
         }
      }

      .footerStrip {
         margin: 0;
         padding: 0;
         width: 100%;

         ul {
            display: flex;
            flex-flow: row wrap;
            gap: 1rem;
            list-style: none;
            margin: 0.5rem 0 0 0;
            padding: 0.5rem 0 0 0;
            text-align: center;

            li {
               margin: 0;
               padding: 0;
            }
         }
      }

      .neocities {
         width: 30px;
      }

      .outbound {
         margin: 0.5rem 0 0 0;
      }
   }

   .feedXML {
      img {
         max-height: 1.5rem;
         width: auto;
      }

      &.right {
         align-self: flex-end;
      }
   }
}

header {
   margin: 1rem 0 0 0;
   position: relative;
   text-align: left;

   @include mq("44", $type: max) {
      padding: 1rem;
   }

   @include mq("44") {
      padding: 1rem 2rem 0 1.5rem;
      vertical-align: baseline;
   }

   &:not(.home) {
      h1:hover {
         &::after {
            display: none;
         }
      }
   }

   h1 {
      @include font-family-title;
      background-clip: text;
      -webkit-background-clip: text;
      font-size: clamp(1.5rem, 5vw, 5rem);
      font-weight: 300;
      letter-spacing: clamp(0.01rem, 3vw, 2rem);
      margin: 0 0 0 clamp(-0.01rem, -3vw, -2rem);
      max-width: 78rem;
      padding: 0 0 0 clamp(0.01rem, 3vw, 2rem);
      position: relative;
      text-align: center;
      text-indent: clamp(0.01rem, 3vw, 2rem);
      text-transform: uppercase;
      z-index: 1;

      &::after {
         background-image: var(--noise);
         background-clip: text;
         -webkit-background-clip: text;
         content: 'DH:PERSONAL';
         height: 100%;
         left: 0;
         letter-spacing: clamp(0.01rem, 3vw, 2rem);
         mix-blend-mode: overlay;
         padding: 0 0 0 clamp(0.3rem, 1.5vw, 1rem);
         position: absolute;
         text-align: center;
         top: 0;
         width: 100%;
         z-index: 0;
      }

      a,
      a:link,
      a:hover,
      a:active,
      a:visited {
         color: inherit !important;
         text-decoration: none;
      }

      span {
         letter-spacing: 0;
      }
   }

   cite {
      display: block;
      margin: 1rem 0 0 0;
      transition: color 1.5s;
   }
}

