main {
   blockquote {
      line-height: inherit;
      margin: 1rem 0 0 0;
      padding: 0.25rem 0.5rem 0.5rem 1rem;
      position: relative;

      @include mq("33") {
         padding: 0.25rem 2rem 0.5rem 1rem;
      }

