   }
}

@media print {

   .hideNav,
   nav,
   footer,
   hr,
   button,
   .button,
   .embedtool,
   iframe {
      display: none;
      height: 0;
      visibility: hidden;
      width: 0;
   }

   header,
   main,
   aside {
      margin: 0;
      max-width: unset;
      padding: 0;
   }

   main {
      blockquote {
         border-left: 2px solid black;
      }
   }
}